:host {
  display: block;
  height: 100%;
}

.query-preview-panel {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  overflow: hidden;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.875rem 1rem;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 0.875rem;
  color: #374151;

  i {
    font-size: 1rem;
    color: #6b7280;
  }
}

.confidence-badge {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 0.25rem 0.625rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;

  &[data-level='high'] {
    background-color: #dcfce7;
    color: #166534;
  }

  &[data-level='medium'] {
    background-color: #fef3c7;
    color: #92400e;
  }

  &[data-level='low'] {
    background-color: #fee2e2;
    color: #991b1b;
  }

  .confidence-value {
    font-weight: 700;
    font-size: 0.875rem;
  }

  .confidence-label {
    font-size: 0.625rem;
    text-transform: uppercase;
    letter-spacing: 0.025em;
  }
}

.query-content {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
}

.loading-state {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 2rem;
  color: #6b7280;
  font-size: 0.875rem;

  i {
    font-size: 1.25rem;
    color: #3b82f6;
  }
}

.query-text {
  font-size: 0.9375rem;
  line-height: 1.6;
  color: #1f2937;
  margin: 0;
  padding: 0.75rem;
  background-color: #f3f4f6;
  border-radius: 0.375rem;
  border-left: 3px solid #3b82f6;
}

.changes-section {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e5e7eb;
}

.changes-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}

.changes-list {
  margin: 0.5rem 0 0 0;
  padding: 0;
  list-style: none;
}

.change-item {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.8125rem;
  color: #059669;
  margin-bottom: 0.25rem;

  i {
    font-size: 0.75rem;
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  text-align: center;
  height: 100%;
}

.empty-state-icon {
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 50%;
  margin-bottom: 1rem;

  i {
    font-size: 1.5rem;
    color: #3b82f6;
  }
}

.empty-state-title {
  margin: 0 0 0.5rem 0;
  font-size: 0.9375rem;
  font-weight: 600;
  color: #374151;
}

.empty-state-description {
  margin: 0 0 1.25rem 0;
  font-size: 0.8125rem;
  color: #6b7280;
  max-width: 200px;
}

.empty-state-steps {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  width: 100%;
  max-width: 220px;
}

.step {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  padding: 0.5rem 0.75rem;
  background-color: #f9fafb;
  border-radius: 0.375rem;
  text-align: left;

  .step-number {
    width: 1.25rem;
    height: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e5e7eb;
    border-radius: 50%;
    font-size: 0.6875rem;
    font-weight: 600;
    color: #6b7280;
    flex-shrink: 0;
  }

  .step-text {
    font-size: 0.6875rem;
    color: #6b7280;
    line-height: 1.3;
  }
}

.data-status-section {
  padding: 1rem;
  background-color: #f9fafb;
  border-top: 1px solid #e5e7eb;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 0.8125rem;
  color: #374151;
  margin-bottom: 0.75rem;

  i {
    font-size: 0.875rem;
    color: #6b7280;
  }
}

.collection-progress {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;

  .progress-label {
    display: block;
    margin-top: 0.375rem;
    font-size: 0.75rem;
    color: #6b7280;
  }
}

.data-types-label {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
  color: #374151;

  i {
    font-size: 0.75rem;
    color: #6b7280;
  }
}

.progress-bar-container {
  ::ng-deep .collection-progress-bar {
    height: 0.5rem;
    border-radius: 0.25rem;
  }
}

.status-message {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.75rem;
  color: #3b82f6;

  i {
    font-size: 0.75rem;
  }
}

.collection-stats {
  display: flex;
  gap: 1rem;
  padding: 0.5rem 0;
  border-top: 1px solid #e5e7eb;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;

  .stat-value {
    font-size: 0.875rem;
    font-weight: 600;
    color: #1f2937;
  }

  .stat-label {
    font-size: 0.625rem;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #6b7280;
  }
}

.vehicle-progress-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  max-height: 200px;
  overflow-y: auto;
  padding: 0.5rem 0;
  border-top: 1px solid #e5e7eb;
}

.vehicle-progress-item {
  padding: 0.5rem;
  background-color: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  font-size: 0.75rem;

  &[data-status='collecting'] {
    border-left: 3px solid #3b82f6;
    background-color: #eff6ff;
  }

  &[data-status='complete'] {
    border-left: 3px solid #10b981;
    background-color: #f0fdf4;
  }

  &[data-status='error'] {
    border-left: 3px solid #ef4444;
    background-color: #fef2f2;
  }
}

.vehicle-header {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  margin-bottom: 0.375rem;

  .status-icon {
    font-size: 0.75rem;

    &.pending {
      color: #9ca3af;
    }

    &.collecting {
      color: #3b82f6;
    }

    &.complete {
      color: #10b981;
    }

    &.error {
      color: #ef4444;
    }
  }

  .vehicle-name {
    flex: 1;
    font-weight: 500;
    color: #374151;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .vehicle-records {
    font-size: 0.6875rem;
    color: #6b7280;
    white-space: nowrap;
  }
}

.data-type-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}

.data-type-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.125rem 0.375rem;
  font-size: 0.625rem;
  font-weight: 500;
  border-radius: 0.25rem;
  text-transform: capitalize;

  i {
    font-size: 0.625rem;
  }

  &[data-status='pending'] {
    background-color: #f3f4f6;
    color: #6b7280;
  }

  &[data-status='collecting'] {
    background-color: #dbeafe;
    color: #1d4ed8;
  }

  &[data-status='complete'] {
    background-color: #dcfce7;
    color: #166534;
  }

  &[data-status='skipped'] {
    background-color: #f3f4f6;
    color: #9ca3af;
  }
}

.collected-data {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.collection-summary {
  padding: 0.75rem;
  background-color: #f0fdf4;
  border: 1px solid #bbf7d0;
  border-radius: 0.5rem;
}

.summary-header {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.8125rem;
  font-weight: 600;
  color: #166534;
  margin-bottom: 0.5rem;

  i {
    font-size: 0.875rem;
    color: #22c55e;
  }
}

.summary-totals {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.total-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.375rem 0.75rem;
  background-color: #ffffff;
  border-radius: 0.375rem;
  min-width: 60px;

  .total-value {
    font-size: 1rem;
    font-weight: 700;
    color: #166534;
  }

  .total-label {
    font-size: 0.625rem;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #6b7280;
  }
}

.vehicle-breakdown {
  border-top: 1px solid #e5e7eb;
  padding-top: 0.75rem;
}

.breakdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 0.75rem;
  font-weight: 600;
  color: #374151;

  .vehicle-count {
    font-weight: 400;
    color: #6b7280;
  }
}

.vehicle-progress-list.completed {
  max-height: 250px;
}

.data-item {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.8125rem;

  &.success {
    color: #059669;

    i {
      color: #10b981;
    }
  }

  &.skipped {
    color: #6b7280;

    i {
      color: #9ca3af;
    }
  }
}

// Dark mode support
:host-context(.dark),
:host-context([data-theme='dark']) {
  .query-preview-panel {
    background-color: #1f2937;
    border-color: #374151;
  }

  .panel-header {
    background-color: #111827;
    border-bottom-color: #374151;
  }

  .header-title {
    color: #e5e7eb;

    i {
      color: #9ca3af;
    }
  }

  .query-text {
    background-color: #374151;
    color: #f3f4f6;
    border-left-color: #3b82f6;
  }

  .changes-section {
    border-top-color: #374151;
  }

  .empty-state-icon {
    background: linear-gradient(135deg, #1e3a5f 0%, #1e40af 100%);

    i {
      color: #60a5fa;
    }
  }

  .empty-state-title {
    color: #e5e7eb;
  }

  .empty-state-description {
    color: #9ca3af;
  }

  .step {
    background-color: #374151;

    .step-number {
      background-color: #4b5563;
      color: #9ca3af;
    }

    .step-text {
      color: #9ca3af;
    }
  }

  .data-status-section {
    background-color: #111827;
    border-top-color: #374151;
  }

  .section-header {
    color: #e5e7eb;

    i {
      color: #9ca3af;
    }
  }

  .data-types-label {
    color: #e5e7eb;

    i {
      color: #9ca3af;
    }
  }

  .status-message {
    color: #60a5fa;
  }

  .collection-stats {
    border-top-color: #374151;

    .stat-value {
      color: #f3f4f6;
    }
  }

  .vehicle-progress-list {
    border-top-color: #374151;
  }

  .vehicle-progress-item {
    background-color: #374151;
    border-color: #4b5563;

    &[data-status='collecting'] {
      background-color: #1e3a5f;
    }

    &[data-status='complete'] {
      background-color: #064e3b;
    }

    &[data-status='error'] {
      background-color: #7f1d1d;
    }
  }

  .vehicle-header {
    .vehicle-name {
      color: #e5e7eb;
    }

    .vehicle-records {
      color: #9ca3af;
    }
  }

  .data-type-badge {
    &[data-status='pending'] {
      background-color: #374151;
      color: #9ca3af;
    }

    &[data-status='collecting'] {
      background-color: #1e40af;
      color: #93c5fd;
    }

    &[data-status='complete'] {
      background-color: #065f46;
      color: #a7f3d0;
    }
  }

  // Completed state dark mode
  .collection-summary {
    background-color: #064e3b;
    border-color: #065f46;
  }

  .summary-header {
    color: #a7f3d0;

    i {
      color: #34d399;
    }
  }

  .total-item {
    background-color: #1f2937;

    .total-value {
      color: #a7f3d0;
    }

    .total-label {
      color: #9ca3af;
    }
  }

  .vehicle-breakdown {
    border-top-color: #374151;
  }

  .breakdown-header {
    color: #e5e7eb;

    .vehicle-count {
      color: #9ca3af;
    }
  }
}
